.statistics {
  text-align: center;
  max-width: 300px;
  background-color: #53f03e;
  margin: 15px auto;
  border-radius: 5px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  margin: 0;
  font-size: 18px;
  color: rgb(2, 0, 0);
  text-transform: uppercase;
}

.stat-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 60px;
  box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.2);
  line-height: 1.6;
}

.item:first-child {
  border-radius: 0 0 0 5px;
}

.item:last-child {
  border-radius: 0 0 5px 0;
}

.label {
  font-size: 15px;
  color: #000000;
}

.percentage {
  color: #1714b1;
}
